import { Controller, CangjieClipboardEvent } from '@ali/4ever-cangjie';
import ClipboardPad from '../../utils/ClipboardPad';
import { ClipboardPluginConfig } from '../../utils/types';

export default function createGetClipboardData(clipboard: ClipboardPluginConfig) {
  return function (
    controller: Controller,
    event: CangjieClipboardEvent,
  ) {
    const clipboardPad = ClipboardPad.getCachedClipboardPad(clipboard.key);
    return clipboardPad.getClipboardData(
      (event.nativeEvent || event) as ClipboardEvent,
    );
  };
}
